.tree-search-container {
  width: 100%;
}

.tree-search-input {
  width: 100%;
}

.clear-search-icon {
  right: 8px !important;
  left: auto !important;
  z-index: 10;

  &:focus {
    outline: 2px solid var(--primary-color, #3b82f6);
    outline-offset: 2px;
    border-radius: 2px;
  }
}

.search-results-panel {
  box-shadow:
    0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  max-height: 320px;
  z-index: 1050;
  background: var(--input-background-color) !important;
  border-color: var(--input-border-color) !important;
}

.search-results-list {
  max-height: 280px;
  overflow-y: auto;
}

.search-result-item {
  transition: all 0.15s ease-in-out;

  &.selected {
    background-color: var(--primary-color, #3b82f6) !important;
    color: white;

    .text-gray-900,
    .text-gray-600,
    .text-gray-500,
    .text-gray-400 {
      color: rgba(255, 255, 255, 0.95) !important;
    }

    .bg-gray-50,
    .bg-gray-100 {
      background-color: rgba(255, 255, 255, 0.25) !important;
      color: rgba(255, 255, 255, 0.95) !important;
    }

    i {
      color: rgba(255, 255, 255, 0.9) !important;
      opacity: 1 !important;
    }

    span {
      color: rgba(255, 255, 255, 0.95) !important;
      opacity: 1 !important;
    }

    .metadata-label {
      color: rgba(255, 255, 255, 0.9);
    }

    .metadata-divider {
      background-color: rgba(255, 255, 255, 0.4);
    }

    .metadata-value {
      color: rgba(255, 255, 255, 0.95);
    }

    .field-badge {
      background-color: rgba(255, 255, 255, 0.25);
      color: rgba(255, 255, 255, 0.95);
    }
  }

  &:hover:not(.selected) {
    background-color: #eff6ff;

    i {
      opacity: 0.7;
    }
  }

  &:focus {
    outline: none;
  }

  i {
    flex-shrink: 0;
  }
}

.metadata-table {
  display: flex;
  flex-direction: column;
  gap: 0.375rem;
  margin-top: 0.5rem;
}

.metadata-row {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  min-height: 1.25rem;
}

.metadata-label {
  font-weight: 600;
  color: #9ca3af;
  min-width: 3.5rem;
  flex-shrink: 0;
  opacity: 0.8;
}

.metadata-divider {
  width: 1px;
  height: 1rem;
  background-color: #e5e7eb;
  flex-shrink: 0;
  opacity: 0.6;
}

.metadata-value {
  color: #374151;
  flex: 1;
  min-width: 0;
}

.field-badge {
  display: inline-block;
  background-color: #f3f4f6;
  color: #6b7280;
  padding: 0.125rem 0.5rem;
  border-radius: 0.25rem;
  font-size: 0.6875rem;
  font-weight: 500;
}
